import React from "react";
import * as DialogPrimitive from "@radix-ui/react-dialog";
import { X } from "lucide-react";
import { cn } from "@/utils/cn";
import { Button } from "../Button";

export interface ModalProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  icon?: React.ReactNode;
  title?: React.ReactNode;
  children: React.ReactNode;
  footer?: React.ReactNode;
  className?: string;
  showCloseButton?: boolean;
  size?: "sm" | "md" | "lg";
}

const sizeClasses = {
  sm: "max-w-sm",
  md: "max-w-md",
  lg: "max-w-lg",
};

/**
 *        .
 *
 * {@link Modal}      .
 *    ,     
 *      .
 *
 * Radix UI Dialog      
 *  .
 *
 * ## When (  )
 *
 * **  :**
 * - **  **: , ,       
 * - **  **:         
 * - ** **:     , ,  
 * - ** **:        
 *
 * **   :**
 * - **  **: Toast  
 * - **  **:   
 * - **  **:    
 * - **  **: Popover Tooltip 
 *
 * ## Layout behavior
 *
 *     ,     .
 *  `size` prop  :
 * - `sm`:   24rem (384px) -   
 * - `md`:   28rem (448px) - ,   
 * - `lg`:   32rem (512px) -    
 *
 *      ,    .
 *
 * ## Usage guidelines
 *
 * ###  Do ( )
 *
 * - **  **:        
 *   ```tsx
 *   <Modal title=" ">...</Modal>  // Good
 *   <Modal title="">...</Modal>        // Bad -  
 *   ```
 *
 * - **  **:      
 *   ```tsx
 *   <Button></Button>  // Good
 *   <Button></Button>      // Bad -   
 *   ```
 *
 * - **  **:    size 
 * - **  **: X ,  ,      
 * - **  **: ConfirmModal, SuccessModal, ErrorModal  
 *
 * ###  Don't (/ )
 *
 * - **    ** - UX  
 * - **    ** -     
 * - **   ** -   ( )   
 * - **    ** -    
 * - **  (lg)  ** -       
 *
 * ## Accessibility
 *
 *   WAI-ARIA Dialog  :
 *
 * - ** **:
 *   - `Esc`:  
 *   - `Tab`:     (   )
 *   -         
 *
 * - ** **:
 *   - `role="dialog"`:  
 *   - `aria-labelledby`: title   
 *   - `aria-describedby`: children   
 *
 * - ** **:       
 * - **  **:   body   
 *
 * ## Example
 *
 * {@tool snippet}
 *    :
 *
 * ```tsx
 * function DeleteConfirmDialog() {
 *   const [open, setOpen] = useState(false);
 *
 *   return (
 *     <>
 *       <Button onClick={() => setOpen(true)}></Button>
 *       <Modal
 *         open={open}
 *         onOpenChange={setOpen}
 *         title=" "
 *         footer={
 *           <div className="flex gap-2">
 *             <Button onClick={() => setOpen(false)} variant="outline">
 *               
 *             </Button>
 *             <Button onClick={handleDelete} variant="destructive">
 *               
 *             </Button>
 *           </div>
 *         }
 *       >
 *           ?     .
 *       </Modal>
 *     </>
 *   );
 * }
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *     :
 *
 * ```tsx
 * <Modal
 *   open={open}
 *   onOpenChange={setOpen}
 *   icon={<CheckCircle className="w-12 h-12 text-green-500" />}
 *   title=" "
 *   footer={
 *     <Button onClick={() => setOpen(false)} className="w-full">
 *       
 *     </Button>
 *   }
 * >
 *     .
 * </Modal>
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *     :
 *
 * ```tsx
 * <Modal
 *   open={open}
 *   onOpenChange={setOpen}
 *   title="  "
 *   size="lg"
 *   footer={
 *     <div className="flex justify-end gap-2">
 *       <Button variant="outline" onClick={() => setOpen(false)}>
 *         
 *       </Button>
 *       <Button onClick={handleSubmit}></Button>
 *     </div>
 *   }
 * >
 *   <form className="space-y-4">
 *     <TextInput label="" required />
 *     <TextInput label="" type="email" required />
 *     <TextInput label="" />
 *   </form>
 * </Modal>
 * ```
 * {@end-tool}
 *
 * See also:
 *
 * - {@link ConfirmModal},       
 * - {@link SuccessModal},    
 * - {@link ErrorModal},    
 * - {@link WarningModal},    
 * - {@link DeleteModal},    
 * - {@link Toast},     
 * - {@link Popover},     
 */
export const Modal = React.forwardRef<HTMLDivElement, ModalProps>(
  (
    {
      open,
      onOpenChange,
      icon,
      title,
      children,
      footer,
      className,
      showCloseButton = true,
      size = "md",
    },
    ref,
  ) => {
    return (
      <DialogPrimitive.Root open={open} onOpenChange={onOpenChange}>
        <DialogPrimitive.Portal>
          <DialogPrimitive.Overlay
            className={cn(
              "fixed inset-0 z-150 bg-black/50",
              "data-[state=open]:animate-in data-[state=closed]:animate-out",
              "data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0",
            )}
          />
          <DialogPrimitive.Content
            ref={ref}
            className={cn(
              "fixed left-[50%] top-[50%] z-150",
              "translate-x-[-50%] translate-y-[-50%]",
              "w-full",
              sizeClasses[size],
              "bg-white rounded-lg shadow-lg",
              "p-6",
              "data-[state=open]:animate-in data-[state=closed]:animate-out",
              "data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0",
              "data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95",
              className,
            )}
          >
            {showCloseButton && (
              <DialogPrimitive.Close asChild>
                <Button
                  variant="ghost"
                  size="icon"
                  className={cn("h-6 w-6", "absolute right-4 top-4")}
                >
                  <X />
                  <span className="sr-only">Close</span>
                </Button>
              </DialogPrimitive.Close>
            )}

            {icon && <div className="flex justify-center mb-4">{icon}</div>}

            {title && (
              <DialogPrimitive.Title
                className={cn(
                  "text-lg font-bold text-cms-gray-900 mb-2",
                  "flex items-center justify-center",
                )}
              >
                {title}
              </DialogPrimitive.Title>
            )}

            <DialogPrimitive.Description className="text-sm text-cms-gray-700 text-center">
              {children}
            </DialogPrimitive.Description>

            {footer && <div className="mt-6">{footer}</div>}
          </DialogPrimitive.Content>
        </DialogPrimitive.Portal>
      </DialogPrimitive.Root>
    );
  },
);

Modal.displayName = "Modal";
