import { cn } from "@/utils/cn";
import { cva, VariantProps } from "class-variance-authority";
import { ButtonHTMLAttributes, forwardRef } from "react";

/**
 *    variant .
 *
 * @variant default -   ( )
 * @variant secondary -   (  )
 * @variant outline -   ( )
 * @variant ghost -   ( , hover )
 * @variant link -   ()
 *
 * @size default -   (h-10)
 * @size sm -   (h-8)
 * @size lg -   (h-11)
 * @size icon -   ( 10x10)
 */
export const buttonVariants = cva(
  cn(
    "inline-flex items-center justify-center gap-2",
    "rounded-md ring-offset-white",
    "text-sm font-medium",
    "focus-visible:outline-none focus-visible:ring-2",
    "focus-visible:ring-slate-950 focus-visible:ring-offset-2",
    "disabled:pointer-events-none disabled:opacity-60",
    "transition-colors cursor-pointer",
  ),
  {
    variants: {
      variant: {
        default:
          "border-0 bg-cms-gray-850 text-cms-white hover:bg-cms-gray-750",
        secondary: cn(
          "border-0 bg-cms-gray-300 text-cms-black",
          "hover:bg-cms-gray-200 hover:text-cms-gray-800",
        ),
        outline: cn(
          "border border-cms-gray-400 bg-transparent",
          "hover:bg-cms-gray-200 hover:text-cms-gray-900",
        ),

        ghost:
          "border-0 bg-transparent hover:bg-cms-gray-200 hover:text-cms-gray-800",
        link: "border-0 text-cms-black underline-offset-4 hover:underline",
      },
      size: {
        default: "h-10 px-4 py-2",
        sm: "h-8 rounded-sm px-3",
        lg: "h-11 rounded-sm px-8",
        icon: "h-10 w-10",
      },
    },
    defaultVariants: {
      variant: "default",
      size: "default",
    },
  },
);

export interface ButtonProps
  extends
    ButtonHTMLAttributes<HTMLButtonElement>,
    VariantProps<typeof buttonVariants> {}

/**
 *           .
 *
 * {@link Button}   (Variant) (Size)  
 *       .
 *
 * ## When (  )
 * ###   
 *
 * - ** **:  , ,        
 * - **   (CTA)**:       
 * - ****:       
 * - ** **:  ,    UI   
 *
 * ###    :
 *
 * - ** **:        `<a>`   Link  
 * - **    **: `RadioButton` `Checkbox` 
 * - ** **: `SideNavigation`      
 *
 * ---
 *
 * ## Layout behavior
 *
 * - **Inline-flex**:         .
 * - **W-full**: `fullWidth` prop         .
 * - **Center Alignment**:       .
 * - **Responsive**:    `fullWidth`    .
 *
 * ---
 *
 * ## Usage guidelines
 *
 * ###  Do ( )
 *
 * - **  **: '', '', ''      .
 * - **  **:         'primary' variant .
 * - ** **:   ,         .
 * - **  **:        `isLoading`     .
 *
 * ###  Don't (/ )
 *
 * - **  **:   (Icon Button)     `aria-label` .
 * - **  **:       ( 2-4).
 * - ** **: '' ' '      .
 *
 * ---
 *
 * ## Accessibility
 *
 * - **Keyboard Support**: `Enter` `Space`   ,     .
 * - **ARIA Attributes**: `disabled`   `aria-disabled`  .
 * - **Role**:  `<button>`  ,   `asChild`   (Link )      .
 *
 * ---
 *
 * ## Example
 *
 * {@tool snippet}
 *    :
 *
 * ```tsx
 * <div className="flex gap-2">
 *   <Button variant="primary"></Button>
 *   <Button variant="outline"></Button>
 *   <Button variant="ghost"></Button>
 * </div>
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *     :
 *
 * ```tsx
 * <Button
 *   isLoading={isSubmitting}
 *   leftIcon={<SaveIcon />}
 *   onClick={handleSubmit}
 * >
 *    
 * </Button>
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *   :
 *
 * ```tsx
 * <div className="flex items-center gap-2">
 *   <Button size="sm">Small</Button>
 *   <Button size="md">Medium</Button>
 *   <Button size="lg">Large</Button>
 * </div>
 * ```
 * {@end-tool}
 *s
 * See also:
 *
 * - {@link TextInput},     
 * - {@link Modal},      
 * - {@link IconButton},     
 */
const Button = forwardRef<HTMLButtonElement, ButtonProps>(
  ({ className, variant, size, ...props }, ref) => {
    return (
      <button
        className={cn(buttonVariants({ variant, size, className }))}
        ref={ref}
        {...props}
      />
    );
  },
);
Button.displayName = "Button";

export { Button };
