import { cn } from "@/utils/cn";
import { forwardRef } from "react";
import { Dropdown, DropdownProps } from "./Dropdown";

export interface SelectProps extends Omit<
  DropdownProps,
  "multiple" | "searchable" | "clearable"
> {
  label?: string;
  helperText?: string;
  error?: string;
  required?: boolean;
}

/**
 *  , ,        .
 *
 * {@link Select} {@link Dropdown}  , (Form)  
 *  UI (,  ,    )  .
 *          .
 *
 * ## When (  )
 *
 * **  :**
 * - **  **: ,    , ,    
 * - **   **:        
 * - **  **:     (helperText)  
 *
 * **   :**
 * - ** /**:        `Dropdown`  .
 * - ** /**:      `Dropdown`  `Combobox` .
 *
 * ## Layout behavior
 *
 * - **Vertical Stack**:  -   - /   .
 * - **Consistency**:   (TextInput )        .
 *
 * ## Usage guidelines
 *
 * ###  Do ( )
 *
 * - ** **:          .
 * - **  **: `required`       .
 * - **  **:     `error` prop     .
 *
 * ###  Don't (/ )
 *
 * - **  **:          .
 * - ** **:       .     .
 *
 * ## Accessibility
 *
 * - **Label Association**:         .
 * - **Error States**:    `aria-invalid`         .
 *
 * ## Example
 *
 * {@tool snippet}
 *      :
 *
 * ```tsx
 * <Select
 *   label=" "
 *   required={true}
 *   options={[
 *     { value: 'admin', label: '' },
 *     { value: 'editor', label: '' },
 *     { value: 'viewer', label: '' },
 *   ]}
 *   placeholder="  "
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *    :
 *
 * ```tsx
 * <Select
 *   label=" "
 *   options={countryOptions}
 *   error="   "
 * />
 * ```
 * {@end-tool}
 *
 * See also:
 *
 * - {@link Dropdown},       
 * - {@link TextInput},     
 * - {@link RadioButton},      
 */
export const Select = forwardRef<HTMLButtonElement, SelectProps>(
  ({ label, helperText, error, required, className, ...props }, ref) => {
    return (
      <div className={cn("space-y-1", className)}>
        {label && (
          <label className="block text-sm font-medium text-cms-black">
            {label}
            {required && <span className="text-cms-red-500 ml-1">*</span>}
          </label>
        )}

        <Dropdown
          ref={ref}
          {...props}
          className={cn(error && "border-cms-red-500 focus:ring-cms-red-500")}
        />

        {(helperText || error) && (
          <p
            className={cn(
              "text-xs",
              error ? "text-cms-red-500" : "text-cms-gray-400",
            )}
          >
            {error || helperText}
          </p>
        )}
      </div>
    );
  },
);

Select.displayName = "Select";
