import React from "react";
import { cva, type VariantProps } from "class-variance-authority";
import { cn } from "@/utils/cn";

const textInputVariants = cva(
  cn(
    "w-full box-border",
    "px-3 py-2",
    "rounded-cms-sm",
    "border border-solid",
    "font-normal leading-tight",
    "transition-colors duration-200",
    "outline-none",
    "text-md text-cms-black",
    "placeholder:text-cms-gray-500",
    "placeholder:text-md",
  ),
  {
    variants: {
      variant: {
        default: cn(
          "bg-cms-white ",
          "border-cms-gray-450",
          "focus:border-cms-gray-800",
          "disabled:bg-cms-gray-150",
          "disabled:text-cms-gray-400",
          "disabled:cursor-not-allowed",
        ),
        error: cn(
          "bg-cms-white",
          "border-cms-red-400",
          "focus:border-cms-red-500",
        ),
      },
      fullWidth: {
        true: "w-full",
        false: "w-auto",
      },
    },
    defaultVariants: {
      variant: "default",
      fullWidth: true,
    },
  },
);

const labelVariants = cva("block text-md font-medium text-cms-black");

const errorMessageVariants = cva(
  "block text-sm font-medium text-cms-red-400 mt-1",
);

const helperTextVariants = cva(
  "block text-sm font-normal text-cms-gray-700 mt-1",
);

export interface TextInputProps
  extends
    Omit<React.InputHTMLAttributes<HTMLInputElement>, "size">,
    VariantProps<typeof textInputVariants> {
  label?: string;
  required?: boolean;
  error?: boolean;
  errorMessage?: string;
  helperText?: string;
  showCharCount?: boolean;
  labelLayout?: "vertical" | "horizontal";
  labelWidth?: string;
}

/**
 *  , ,       .
 *
 * {@link TextInput}     , label, placeholder,  ,
 * helper ,      .   
 *   .
 *
 * ## When (  )
 *
 * **  :**
 * - **   **: , ,       
 * - **  **: email, password, number, date  HTML input type 
 * - **  **:       
 * - ** **:       
 * - ** **: `required`       
 *
 * ## Layout behavior
 *
 * TextInput     (`fullWidth={true}`) .
 * `fullWidth={false}`     .
 *
 *   `labelLayout` prop :
 * - **vertical** (): Label     .
 * - **horizontal**: Label     . `labelWidth` Label     (: 120px).
 *
 *    :
 * 1. ** ** ( ): label (,   * ) +    ()
 * 2. ** **:   
 * 3. ** ** ( ): errorMessage  helperText
 *
 *  `h-10` (2.5rem / 40px)     .
 *
 * ## Usage guidelines
 *
 * ###  Do ( )
 *
 * - ** label **:     
 * - **  **:     `required`  .
 * - **  **:       `labelLayout="horizontal"`    .
 *
 * ## Example
 *
 * {@tool snippet}
 *      :
 *
 * ```tsx
 * <TextInput
 *   label=" "
 *   required={true}
 *   placeholder=" "
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *   :
 *
 * ```tsx
 * <TextInput
 *   label="()"
 *   required={true}
 *   labelLayout="horizontal"
 *   labelWidth="150px"
 *   placeholder=" "
 * />
 * ```
 * {@end-tool}
 *
 * See also:
 *
 * - {@link Textarea},      
 * - {@link TagInput},    
 * - {@link DatePicker},    
 * - {@link Dropdown},    
 */
export const TextInput = React.forwardRef<HTMLInputElement, TextInputProps>(
  (
    {
      className,
      variant,
      fullWidth,
      label,
      required,
      error,
      errorMessage,
      helperText,
      showCharCount,
      maxLength,
      value,
      defaultValue,
      onChange,
      id,
      labelLayout = "vertical",
      labelWidth = "120px",
      ...props
    },
    ref,
  ) => {
    const [internalValue, setInternalValue] = React.useState<string>(
      (value || defaultValue || "") as string,
    );
    const inputId = id || `input-${Math.random().toString(36).substr(2, 9)}`;
    const finalVariant = error ? "error" : variant;

    const currentValue =
      value !== undefined ? (value as string) : internalValue;
    const charCount = currentValue?.length || 0;

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      if (value === undefined) {
        setInternalValue(e.target.value);
      }
      onChange?.(e);
    };

    const hasHeader = label || (showCharCount && maxLength);
    const isHorizontal = labelLayout === "horizontal";

    return (
      <div className={cn("w-full", !fullWidth && "w-auto")}>
        {isHorizontal && hasHeader ? (
          <div className="flex items-center gap-3">
            {label && (
              <label
                htmlFor={inputId}
                className={cn(labelVariants(), "mb-0 shrink-0")}
                style={{ width: labelWidth }}
              >
                {label}
                {required && <span className="text-cms-red-400 ml-1">*</span>}
              </label>
            )}
            <div className="flex-1">
              <input
                id={inputId}
                ref={ref}
                className={cn(
                  textInputVariants({ variant: finalVariant, fullWidth: true }),
                  className,
                )}
                maxLength={maxLength}
                value={value}
                defaultValue={defaultValue}
                onChange={handleChange}
                required={required}
                {...props}
              />
            </div>
            {showCharCount && maxLength && (
              <span className="text-sm text-cms-gray-600 shrink-0">
                {charCount} / {maxLength}
              </span>
            )}
          </div>
        ) : (
          <>
            {hasHeader && (
              <div className="flex justify-between items-center mb-2">
                {label ? (
                  <label htmlFor={inputId} className={labelVariants()}>
                    {label}
                    {required && (
                      <span className="text-cms-red-400 ml-1">*</span>
                    )}
                  </label>
                ) : (
                  <div />
                )}
                {showCharCount && maxLength && (
                  <span className="text-sm text-cms-gray-600">
                    {charCount} / {maxLength}
                  </span>
                )}
              </div>
            )}
            <input
              id={inputId}
              ref={ref}
              className={cn(
                textInputVariants({ variant: finalVariant, fullWidth }),
                className,
              )}
              maxLength={maxLength}
              value={value}
              defaultValue={defaultValue}
              onChange={handleChange}
              required={required}
              {...props}
            />
          </>
        )}
        {error && errorMessage && (
          <span className={errorMessageVariants()}>{errorMessage}</span>
        )}
        {!error && helperText && (
          <span className={helperTextVariants()}>{helperText}</span>
        )}
      </div>
    );
  },
);

TextInput.displayName = "TextInput";
