import React from "react";
import * as RadioGroupPrimitives from "@radix-ui/react-radio-group";
import { cn } from "@/utils/cn";
import { cva, type VariantProps } from "class-variance-authority";

/**
 *            .
 *
 * {@link RadioGroup}   {@link RadioGroupItem}  ,
 *         .
 *
 * Radix UI Radio Group       
 *     .
 *
 * ## When (  )
 *
 * **  :**
 * - ** **:  ,           
 * - ** **:        ( 5   )
 *
 * **   :**
 * - ** **:         `Checkbox` .
 * - **  **:  5-6     `Dropdown`   .
 * - ** On/Off**:       `Checkbox` `Switch`  .
 *
 * ## Layout behavior
 *
 * - **asChild**: `asChild`           .
 *         .
 *
 * ## Accessibility
 *
 * - **Keyboard Navigation**: `Tab`    ,  (`Up`, `Down`, `Left`, `Right`)       .
 * - **Roles**: `role="radiogroup"`  `role="radio"`   .
 *
 * ## Example
 *
 * {@tool snippet}
 *     :
 *
 * ```tsx
 * <RadioGroup defaultValue="apple" onValueChange={(val) => console.log(val)}>
 *   <div className="flex items-center gap-2">
 *     <RadioGroupItem value="apple" id="apple" />
 *     <label htmlFor="apple"></label>
 *   </div>
 *   <div className="flex items-center gap-2">
 *     <RadioGroupItem value="banana" id="banana" />
 *     <label htmlFor="banana"></label>
 *   </div>
 * </RadioGroup>
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 * `asChild`    :
 *
 * ```tsx
 * <RadioGroup asChild>
 *   <section className="my-custom-group">
 *     <RadioGroupItem asChild value="custom">
 *       <button type="button" className="custom-radio-trigger">
 *          
 *       </button>
 *     </RadioGroupItem>
 *   </section>
 * </RadioGroup>
 * ```
 * {@end-tool}
 *
 * See also:
 *
 * - {@link Checkbox},    
 * - {@link Dropdown},      
 * - {@link Switch},  /  
 */
const RadioGroup = React.forwardRef<
  React.ElementRef<typeof RadioGroupPrimitives.Root>,
  React.ComponentPropsWithoutRef<typeof RadioGroupPrimitives.Root>
>(({ className, ...props }, ref) => {
  return (
    <RadioGroupPrimitives.Root className={className} {...props} ref={ref} />
  );
});
RadioGroup.displayName = RadioGroupPrimitives.Root.displayName;

const radioGroupItemVariants = cva(
  cn(
    "flex items-center justify-center",
    "aspect-square rounded-full border-2 transition-colors",
    "focus:outline-none",
    "focus-visible:ring-offset-2",
    "disabled:cursor-not-allowed disabled:opacity-50",
    "cursor-pointer",
  ),
  {
    variants: {
      variant: {
        black: cn(
          "border-cms-gray-300 text-cms-black",
          "data-[state=checked]:border-cms-black",
        ),
        default: cn(
          "border-cms-gray-300 text-cms-primary-300",
          "data-[state=checked]:border-cms-primary-300",
        ),
        green: cn(
          "border-cms-gray-300 text-cms-green-500",
          "data-[state=checked]:border-cms-green-500",
        ),
        blue: cn(
          "border-cms-gray-300 text-cms-blue-700",
          "data-[state=checked]:border-cms-blue-700",
        ),
        red: cn(
          "border-cms-gray-300 text-cms-red-400",
          "data-[state=checked]:border-cms-red-400",
        ),
      },
      size: {
        sm: "size-4",
        md: "size-5",
        lg: "size-6",
      },
    },
    defaultVariants: {
      variant: "black",
      size: "md",
    },
  },
);

const radioGroupIndicatorVariants = cva(
  "flex items-center justify-center rounded-full bg-current aspect-square",
  {
    variants: {
      variant: {
        // bg-current   text color      
        black: "text-cms-black",
        default: "text-cms-primary-300",
        green: "text-cms-green-500",
        blue: "text-cms-blue-700",
        red: "text-cms-red-400",
      },
      size: {
        sm: "size-2",
        md: "size-2.5",
        lg: "size-3",
      },
    },
    defaultVariants: {
      variant: "black",
      size: "md",
    },
  },
);

export interface RadioGroupItemProps
  extends
    React.ComponentPropsWithoutRef<typeof RadioGroupPrimitives.Item>,
    VariantProps<typeof radioGroupItemVariants> {}

const RadioGroupItem = React.forwardRef<
  React.ElementRef<typeof RadioGroupPrimitives.Item>,
  RadioGroupItemProps
>(({ className, variant, size, ...props }, ref) => {
  return (
    <RadioGroupPrimitives.Item
      ref={ref}
      className={cn(radioGroupItemVariants({ variant, size }), className)}
      {...props}
    >
      <RadioGroupPrimitives.Indicator
        className={cn(radioGroupIndicatorVariants({ variant, size }))}
      />
    </RadioGroupPrimitives.Item>
  );
});
RadioGroupItem.displayName = RadioGroupPrimitives.Item.displayName;

export { RadioGroup, RadioGroupItem };
