import { cn } from "@/utils/cn";
import { useState, forwardRef } from "react";
import { Dropdown, DropdownProps } from "./Dropdown";

export interface ComboboxProps extends Omit<DropdownProps, "searchable"> {
  loading?: boolean;
  createable?: boolean;
  onCreateOption?: (value: string) => void;
}

/**
 *      ,        .
 *
 * {@link Combobox}          ,
 *             .
 *
 * {@link Dropdown}  ,  `searchable`   .
 *
 * ## When (  )
 *
 * **  :**
 * - **  **:         
 * - **  **:           (:  ,    )
 * - ** **:          
 *
 * **   :**
 * - **  **:          `Dropdown` `Select`  .
 * - **  **:           `TextInput` .
 *
 * ## Layout behavior
 *
 * - **Auto-filtering**:       .
 * - **Create Option**: `createable`  ,      ''  .
 *
 * ## Usage guidelines
 *
 * ###  Do ( )
 *
 * - **  **:      `loading`      .
 * - **  **:         .
 * - ** **:           .
 *
 * ###  Don't (/ )
 *
 * - **  **:   .      .
 * - **  **: `onCreateOption`     UI     .       .
 *
 * ## Accessibility
 *
 * - **Aria Roles**: `role="combobox"`        .
 * - **Live Regions**:            .
 *
 * ## Example
 *
 * {@tool snippet}
 *      :
 *
 * ```tsx
 * <Combobox
 *   options={existingTags}
 *   createable={true}
 *   onCreateOption={(newTag) => {
 *     //       
 *     handleCreateTag(newTag);
 *   }}
 *   placeholder="   "
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *    :
 *
 * ```tsx
 * <Combobox
 *   options={[]}
 *   loading={true}
 *   placeholder="  ..."
 * />
 * ```
 * {@end-tool}
 *
 * See also:
 *
 * - {@link Dropdown},  /   
 * - {@link TagInput},       
 * - {@link TextInput},     
 */
export const Combobox = forwardRef<HTMLButtonElement, ComboboxProps>(
  (
    { options, loading = false, createable = false, onCreateOption, ...props },
    ref,
  ) => {
    const [searchTerm] = useState("");

    const filteredOptions = options.filter((option) =>
      option.label.toLowerCase().includes(searchTerm.toLowerCase()),
    );

    const hasExactMatch = filteredOptions.some(
      (option) => option.label.toLowerCase() === searchTerm.toLowerCase(),
    );

    //   createable        
    const optionsWithCreate = [
      ...filteredOptions,
      ...(createable && searchTerm && !hasExactMatch
        ? [
            {
              value: `__create__${searchTerm}`,
              label: `"${searchTerm}" `,
              disabled: false,
            },
          ]
        : []),
    ];

    return (
      <Dropdown
        ref={ref}
        {...props}
        options={optionsWithCreate}
        searchable={true}
        dropdownClassName={cn(loading && "opacity-75", props.dropdownClassName)}
        onValueChange={(value) => {
          if (value.startsWith("__create__")) {
            const createValue = value.replace("__create__", "");
            onCreateOption?.(createValue);
          } else {
            props.onValueChange?.(value);
          }
        }}
      />
    );
  },
);

Combobox.displayName = "Combobox";
