export const TableCaption = React.forwardRef<
  HTMLTableCaptionElement,
  TableCaptionProps
>(({ className, ...props }, ref) => (
  <caption
    ref={ref}
    className={cn("mt-4 text-sm text-cms-gray-600", className)}
    {...props}
  />
));
TableCaption.displayName = "TableCaption";
