import { cn } from "@/utils/cn";
import { useCallback, useState } from "react";
import { useDropzone, Accept } from "react-dropzone";
import { FileUploadIcon, FileIcon, XIcon as CloseIcon } from "../icons";

export interface FileUploadProps {
  value?: File[];
  onChange?: (files: File[]) => void;
  maxFiles?: number;
  maxSize?: number;
  accept?: Accept;
  disabled?: boolean;
  className?: string;
  onError?: (error: string) => void;
}

/**
 *           .
 *
 * {@link FileUpload}         .
 *   ,  ,      .
 *
 * ## When (  )
 *
 * **  :**
 * - ** **: PDF, Word, Excel     
 * - **  **:       
 * - **  **:       
 *
 * **   :**
 * - ** **:      {@link ImageUpload} 
 *
 * ## Example
 *
 * {@tool snippet}
 *   :
 *
 * ```tsx
 * <FileUpload
 *   onChange={(files) => console.log(files)}
 *   maxFiles={1}
 *   maxSize={10 * 1024 * 1024} // 10MB
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *    (PDF):
 *
 * ```tsx
 * <FileUpload
 *   onChange={(files) => console.log(files)}
 *   maxFiles={5}
 *   accept={{ "application/pdf": [".pdf"] }}
 * />
 * ```
 * {@end-tool}
 */
export const FileUpload = ({
  value = [],
  onChange,
  maxFiles = 5,
  maxSize = 10 * 1024 * 1024, // 10MB
  accept,
  disabled = false,
  className,
  onError,
}: FileUploadProps) => {
  const [files, setFiles] = useState<File[]>(value);

  const onDrop = useCallback(
    (acceptedFiles: File[], rejectedFiles: any[]) => {
      if (rejectedFiles.length > 0) {
        const error = rejectedFiles[0].errors[0];
        if (error.code === "file-too-large") {
          onError?.(
            `  ${maxSize / 1024 / 1024}MB   .`,
          );
        } else if (error.code === "file-invalid-type") {
          onError?.("   .");
        } else if (error.code === "too-many-files") {
          onError?.(` ${maxFiles}    .`);
        }
        return;
      }

      const newFiles = [...files, ...acceptedFiles].slice(0, maxFiles);
      setFiles(newFiles);
      onChange?.(newFiles);
    },
    [files, maxFiles, maxSize, onChange, onError],
  );

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept,
    maxSize,
    maxFiles,
    disabled,
    multiple: maxFiles > 1,
  });

  const removeFile = (index: number) => {
    const newFiles = files.filter((_, i) => i !== index);
    setFiles(newFiles);
    onChange?.(newFiles);
  };

  const formatFileSize = (bytes: number): string => {
    if (bytes < 1024) return `${bytes} B`;
    if (bytes < 1024 * 1024) return `${(bytes / 1024).toFixed(1)} KB`;
    return `${(bytes / 1024 / 1024).toFixed(1)} MB`;
  };

  const isMaxReached = files.length >= maxFiles;

  return (
    <div className={cn("w-full", className)}>
      {!isMaxReached && (
        <div
          {...getRootProps()}
          className={cn(
            "relative rounded-md border-2 border-dashed",
            "transition-colors cursor-pointer",
            "flex flex-col items-center justify-center",
            "min-h-[200px] p-6",
            isDragActive
              ? "border-cms-black bg-cms-gray-100"
              : "border-cms-gray-300 bg-white hover:bg-cms-gray-50",
            disabled && "opacity-50 cursor-not-allowed pointer-events-none",
          )}
        >
          <input {...getInputProps()} />
          <FileUploadIcon className="text-cms-gray-400" />
          <p className="mt-4 text-sm font-medium text-cms-black text-center">
            {isDragActive
              ? "  "
              : "  "}
          </p>
          <p className="mt-1 text-xs text-cms-gray-400 text-center">
             {maxFiles} ,  {maxSize / 1024 / 1024}MB
          </p>
        </div>
      )}

      {files.length > 0 && (
        <div className={cn("space-y-1.5", isMaxReached ? "" : "mt-4")}>
          {files.map((file, index) => (
            <div
              key={index}
              className={cn(
                "flex items-center gap-2 px-3 py-2",
                "rounded-md border border-cms-gray-300",
                "bg-white hover:bg-cms-gray-50",
                "transition-colors group",
              )}
            >
              <FileIcon className="w-8 h-8" />
              <div className="flex-1 min-w-0">
                <p className="text-sm font-medium text-cms-black truncate leading-tight">
                  {file.name}
                </p>
                <p className="text-xs text-cms-gray-400 leading-tight">
                  {formatFileSize(file.size)}
                </p>
              </div>
              <button
                type="button"
                onClick={() => removeFile(index)}
                className={cn(
                  "w-7 h-7 rounded-full shrink-0",
                  "flex items-center justify-center",
                  "text-cms-gray-400",
                  "hover:bg-cms-gray-100 hover:text-cms-black",
                  "transition-colors",
                  "border-none",
                )}
                aria-label=" "
              >
                <CloseIcon className="w-4 h-4" />
              </button>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

FileUpload.displayName = "FileUpload";
