import React, { useState, useEffect, useMemo, useRef } from "react";
import * as PopoverPrimitive from "@radix-ui/react-popover";
import { Clock } from "lucide-react";
import { cn } from "@/utils/cn";

export interface TimePickerProps {
  value?: string;
  onChange?: (time: string) => void;
  label?: string;
  placeholder?: string;
  format?: "24h" | "12h";
  disabled?: boolean;
  error?: boolean;
  errorMessage?: string;
  helperText?: string;
  className?: string;
  minuteStep?: number;
  showIcon?: boolean;
}

/**
 *       .
 *
 * {@link TimePicker}         ,
 * 24  12(AM/PM)   .
 *
 * `@radix-ui/react-popover`  ,      .
 *
 * ## When (  )
 *
 * **  :**
 * - **  **:  ,  ,  /     
 * - **  **:           
 * - **  **: 5, 10, 15     (minuteStep )
 *
 * **   :**
 * - ** **: '', '', ''       `Dropdown` `RadioButton`    .
 * - **  **:    ,       .
 *
 * ## Layout behavior
 *
 * - **Popover **:        ,    .
 * - ** **:        .
 * - **Full Width**: `className`        .
 *
 * ## Usage guidelines
 *
 * ###  Do ( )
 *
 * - ** Format**:   (: 24, : 12) .
 * - **Minute Step **:      `minuteStep`  UX .
 * - **   **:     ,     .
 *
 * ###  Don't (/ )
 *
 * - **Read-only **:       ,  UI   .
 * - ** Step**: `minuteStep`        .
 *
 * ## Accessibility
 *
 * - **Keyboard Navigation**:      , `Esc`    .
 * - **Focus Management**:        .
 * - **Aria Labels**:         .
 *
 * ## Example
 *
 * {@tool snippet}
 *     (24 ):
 *
 * ```tsx
 * const [time, setTime] = useState("");
 *
 * <TimePicker
 *   label=" "
 *   value={time}
 *   onChange={setTime}
 *   placeholder=" "
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 * 12   15  :
 *
 * ```tsx
 * <TimePicker
 *   label=" "
 *   format="12h"
 *   minuteStep={15}
 *   value="2:30 PM"
 *   onChange={setTime}
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *  :
 *
 * ```tsx
 * <TimePicker
 *   label=" "
 *   error={true}
 *   errorMessage=" (9:00-18:00)  "
 * />
 * ```
 * {@end-tool}
 *
 * See also:
 *
 * - {@link DatePicker},    
 * - {@link DateRangePicker},     
 * - {@link TextInput},     
 */
export const TimePicker = React.forwardRef<HTMLDivElement, TimePickerProps>(
  (
    {
      value,
      onChange,
      label,
      placeholder = "HH:MM",
      format = "24h",
      disabled = false,
      error = false,
      errorMessage,
      helperText,
      className,
      minuteStep = 1,
      showIcon = true,
    },
    ref,
  ) => {
    const [isOpen, setIsOpen] = useState(false);
    const [selectedHour, setSelectedHour] = useState<number | null>(null);
    const [selectedMinute, setSelectedMinute] = useState<number | null>(null);
    const [selectedPeriod, setSelectedPeriod] = useState<"AM" | "PM">("AM");

    const hourScrollRef = useRef<HTMLDivElement>(null);
    const minuteScrollRef = useRef<HTMLDivElement>(null);

    // Parse value and set internal state
    useEffect(() => {
      if (!value) {
        setSelectedHour(null);
        setSelectedMinute(null);
        setSelectedPeriod("AM");
        return;
      }

      const timeRegex24h = /^(\d{1,2}):(\d{2})$/;
      const timeRegex12h = /^(\d{1,2}):(\d{2})\s*(AM|PM)$/i;

      if (format === "24h") {
        const match = value.match(timeRegex24h);
        if (match) {
          setSelectedHour(parseInt(match[1], 10));
          setSelectedMinute(parseInt(match[2], 10));
        }
      } else {
        const match = value.match(timeRegex12h);
        if (match) {
          let hour = parseInt(match[1], 10);
          const period = match[3].toUpperCase() as "AM" | "PM";
          setSelectedHour(hour);
          setSelectedMinute(parseInt(match[2], 10));
          setSelectedPeriod(period);
        }
      }
    }, [value, format]);

    // Generate hour options
    const hours = useMemo(() => {
      if (format === "24h") {
        return Array.from({ length: 24 }, (_, i) => i);
      } else {
        return Array.from({ length: 12 }, (_, i) => i + 1);
      }
    }, [format]);

    // Generate minute options based on step
    const minutes = useMemo(() => {
      const options: number[] = [];
      for (let i = 0; i < 60; i += minuteStep) {
        options.push(i);
      }
      return options;
    }, [minuteStep]);

    // Format display value
    const displayValue = useMemo(() => {
      if (selectedHour === null || selectedMinute === null) return "";

      const formattedMinute = selectedMinute.toString().padStart(2, "0");

      if (format === "24h") {
        const formattedHour = selectedHour.toString().padStart(2, "0");
        return `${formattedHour}:${formattedMinute}`;
      } else {
        return `${selectedHour}:${formattedMinute} ${selectedPeriod}`;
      }
    }, [selectedHour, selectedMinute, selectedPeriod, format]);

    const handleApply = () => {
      if (selectedHour !== null && selectedMinute !== null) {
        onChange?.(displayValue);
        setIsOpen(false);
      }
    };

    const handleCancel = () => {
      // Reset to current value
      if (value) {
        const timeRegex24h = /^(\d{1,2}):(\d{2})$/;
        const timeRegex12h = /^(\d{1,2}):(\d{2})\s*(AM|PM)$/i;

        if (format === "24h") {
          const match = value.match(timeRegex24h);
          if (match) {
            setSelectedHour(parseInt(match[1], 10));
            setSelectedMinute(parseInt(match[2], 10));
          }
        } else {
          const match = value.match(timeRegex12h);
          if (match) {
            setSelectedHour(parseInt(match[1], 10));
            setSelectedMinute(parseInt(match[2], 10));
            setSelectedPeriod(match[3].toUpperCase() as "AM" | "PM");
          }
        }
      } else {
        setSelectedHour(null);
        setSelectedMinute(null);
        setSelectedPeriod("AM");
      }
      setIsOpen(false);
    };

    // Scroll to selected item when popover opens
    useEffect(() => {
      if (isOpen && selectedHour !== null) {
        setTimeout(() => {
          const hourButton = hourScrollRef.current?.querySelector(
            `[data-value="${selectedHour}"]`,
          );
          hourButton?.scrollIntoView({ block: "center" });
        }, 0);
      }
      if (isOpen && selectedMinute !== null) {
        setTimeout(() => {
          const minuteButton = minuteScrollRef.current?.querySelector(
            `[data-value="${selectedMinute}"]`,
          );
          minuteButton?.scrollIntoView({ block: "center" });
        }, 0);
      }
    }, [isOpen, selectedHour, selectedMinute]);

    return (
      <div ref={ref} className={cn("flex flex-col gap-1", className)}>
        {label && (
          <label className="text-sm font-medium text-gray-700">{label}</label>
        )}
        <PopoverPrimitive.Root
          open={isOpen && !disabled}
          onOpenChange={setIsOpen}
        >
          <PopoverPrimitive.Trigger asChild>
            <div className="relative">
              <input
                type="text"
                readOnly
                value={displayValue}
                placeholder={placeholder}
                disabled={disabled}
                className={cn(
                  "w-full h-10 px-3 border rounded bg-white text-sm",
                  "hover:bg-gray-50 hover:border-gray-400",
                  "focus:outline-none",
                  "transition-all duration-150",
                  "cursor-pointer",
                  error ? "border-red-500" : "border-gray-300",
                  disabled &&
                    cn(
                      "bg-gray-100 cursor-not-allowed",
                      "hover:bg-gray-100 hover:border-gray-300",
                    ),
                )}
              />
              {showIcon && (
                <Clock
                  className={cn(
                    "absolute right-0 top-1/2 -translate-y-1/2",
                    "w-4 h-4 text-gray-400",
                    disabled && "opacity-50",
                  )}
                />
              )}
            </div>
          </PopoverPrimitive.Trigger>

          <PopoverPrimitive.Portal>
            <PopoverPrimitive.Content
              align="start"
              sideOffset={5}
              className={cn(
                "z-50 bg-white rounded-lg shadow-xl",
                "border border-gray-200",
                "data-[state=open]:animate-in",
                "data-[state=closed]:animate-out",
                "data-[state=closed]:fade-out-0",
                "data-[state=open]:fade-in-0",
                "data-[state=closed]:zoom-out-95",
                "data-[state=open]:zoom-in-95",
                "data-[side=bottom]:slide-in-from-top-2",
                "data-[side=top]:slide-in-from-bottom-2",
              )}
            >
              <div className="p-4">
                {/* Time Picker Columns */}
                <div className="flex gap-2">
                  {/* Hour Column */}
                  <div className="flex flex-col">
                    <div className="text-xs text-gray-500 text-center mb-2 font-medium">
                      {format === "24h" ? "" : "Hour"}
                    </div>
                    <div
                      ref={hourScrollRef}
                      className="w-16 h-48 overflow-y-auto border border-gray-200 rounded cms-no-scrollbar"
                    >
                      {hours.map((hour) => (
                        <button
                          key={hour}
                          data-value={hour}
                          onClick={() => setSelectedHour(hour)}
                          className={cn(
                            "border-0 cursor-pointer",
                            "w-full h-10 text-sm transition-colors",
                            "hover:bg-gray-100",
                            selectedHour === hour
                              ? "bg-blue-100 text-blue-700 font-medium"
                              : "bg-white text-gray-700",
                          )}
                          aria-label={`${hour}${format === "24h" ? "" : ""}`}
                          aria-selected={selectedHour === hour}
                        >
                          {format === "24h"
                            ? hour.toString().padStart(2, "0")
                            : hour}
                        </button>
                      ))}
                    </div>
                  </div>

                  {/* Minute Column */}
                  <div className="flex flex-col">
                    <div className="text-xs text-gray-500 text-center mb-2 font-medium">
                      {format === "24h" ? "" : "Min"}
                    </div>
                    <div
                      ref={minuteScrollRef}
                      className="w-16 h-48 overflow-y-auto border border-gray-200 rounded cms-no-scrollbar"
                    >
                      {minutes.map((minute) => (
                        <button
                          key={minute}
                          data-value={minute}
                          onClick={() => setSelectedMinute(minute)}
                          className={cn(
                            "border-0 cursor-pointer",
                            "w-full h-10 text-sm transition-colors",
                            "hover:bg-gray-100",
                            selectedMinute === minute
                              ? "bg-blue-100 text-blue-700 font-medium"
                              : "bg-white text-gray-700",
                          )}
                          aria-label={`${minute}${format === "24h" ? "" : " minutes"}`}
                          aria-selected={selectedMinute === minute}
                        >
                          {minute.toString().padStart(2, "0")}
                        </button>
                      ))}
                    </div>
                  </div>

                  {/* AM/PM Column for 12h format */}
                  {format === "12h" && (
                    <div className="flex flex-col">
                      <div className="text-xs text-gray-500 text-center mb-2 font-medium">
                        Period
                      </div>
                      <div className="w-16 flex flex-col gap-1">
                        <button
                          onClick={() => setSelectedPeriod("AM")}
                          className={cn(
                            "border-0 cursor-pointer",
                            "h-10 text-sm rounded transition-colors",
                            "hover:bg-gray-100",
                            selectedPeriod === "AM"
                              ? "bg-blue-100 text-blue-700 font-medium"
                              : "bg-white text-gray-700",
                          )}
                          aria-label="AM"
                          aria-selected={selectedPeriod === "AM"}
                        >
                          AM
                        </button>
                        <button
                          onClick={() => setSelectedPeriod("PM")}
                          className={cn(
                            "border-0 cursor-pointer",
                            "h-10 text-sm rounded transition-colors",
                            "hover:bg-gray-100",
                            selectedPeriod === "PM"
                              ? "bg-blue-100 text-blue-700 font-medium"
                              : "bg-white text-gray-700",
                          )}
                          aria-label="PM"
                          aria-selected={selectedPeriod === "PM"}
                        >
                          PM
                        </button>
                      </div>
                    </div>
                  )}
                </div>
              </div>

              {/* Footer */}
              <div
                className={cn(
                  "flex items-end justify-end px-4 pb-4",
                  "border-t border-gray-200 pt-2",
                )}
              >
                <div className="flex gap-2">
                  <button
                    onClick={handleCancel}
                    className={cn(
                      "w-15 h-8 rounded cursor-pointer",
                      "text-xs font-medium text-gray-700",
                      "border border-gray-300 bg-transparent",
                      "transition-all duration-150",
                      "active:scale-95",
                      "hover:bg-gray-50",
                    )}
                  >
                    
                  </button>
                  <button
                    onClick={handleApply}
                    disabled={selectedHour === null || selectedMinute === null}
                    className={cn(
                      "border-0 cursor-pointer",
                      "w-15 h-8 bg-blue-600 rounded",
                      "text-xs text-white",
                      "hover:bg-blue-700",
                      "active:scale-95",
                      "disabled:bg-gray-300",
                      "disabled:cursor-not-allowed",
                      "disabled:active:scale-100",
                      "transition-all duration-150",
                    )}
                  >
                    
                  </button>
                </div>
              </div>
            </PopoverPrimitive.Content>
          </PopoverPrimitive.Portal>
        </PopoverPrimitive.Root>

        {/* Helper/Error Text */}
        {(helperText || errorMessage) && (
          <div>
            {error && errorMessage ? (
              <p className="text-xs text-red-500">{errorMessage}</p>
            ) : (
              helperText && (
                <p className="text-xs text-gray-500">{helperText}</p>
              )
            )}
          </div>
        )}
      </div>
    );
  },
);

TimePicker.displayName = "TimePicker";
