import React from "react";
import { cva, type VariantProps } from "class-variance-authority";
import { cn } from "@/utils/cn";

const textVariants = cva("cms-font-pretendard cms-text-black", {
  variants: {
    variant: {
      h1: "text-3xl font-bold",
      h2: "text-2xl font-semibold",
      h3: "text-lg font-semibold",
      subtitle: "text-base font-medium",
      body: "text-sm font-normal",
      emphasis: "text-sm font-semibold",
      caption: "text-xs font-normal",
      price: "text-xs font-bold",
    },
    align: {
      left: "text-left",
      center: "text-center",
      right: "text-right",
    },
    decoration: {
      underline: "underline",
      lineThrough: "line-through",
      none: "no-underline",
    },
  },
  defaultVariants: {
    variant: "body",
    align: "left",
  },
});

export interface TextProps
  extends React.HTMLAttributes<HTMLElement>, VariantProps<typeof textVariants> {
  as?: "p" | "span" | "div" | "h1" | "h2" | "h3" | "h4" | "h5" | "h6" | "label";
  children: React.ReactNode;
}

/**
 *       .
 *
 * {@link Text} (Heading), (Body), (Caption)     
 *      (Semantic Structure)     .
 *
 * ## When (  )
 *
 * **  :**
 * - **   **:      
 * - ** **:     
 * - **  **:       
 * - ** **:          
 *
 * **   :**
 * - ** **:         CSS  .
 *
 * ## Layout behavior
 *
 * - **Semantic Tag**: `as` prop   HTML (`h1`, `p`, `span` )    SEO  .
 * - **Alignment**: `align`   , ,      .
 *
 * ## Usage guidelines
 *
 * ###  Do ( )
 *
 * - **  **:  `as="h1"`,  `as="p"`      .
 * - **  **:  (h1)   (h2, h3)    .
 * - **(Variant) **:       `variant`  .
 *
 * ###  Don't (/ )
 *
 * - **   **:       .
 * - **   **:        .
 *
 * ## Example
 *
 * {@tool snippet}
 *    :
 *
 * ```tsx
 * <div className="space-y-4">
 *   <Text variant="h1" as="h1"></Text>
 *   <Text variant="subtitle">  .</Text>
 *   <Text variant="body">
 *         1,234,   5% .
 *   </Text>
 *   <Text variant="caption" align="right"> : 2024-01-24</Text>
 * </div>
 * ```
 * {@end-tool}
 *
 * See also:
 *
 * - {@link TextInput},     
 * - {@link Button},    
 */
export const Text = React.forwardRef<HTMLElement, TextProps>(
  (
    {
      className,
      variant,
      align,
      decoration,
      as: Component = "p",
      children,
      ...props
    },
    ref,
  ) => {
    return (
      <Component
        className={cn(textVariants({ variant, align, decoration }), className)}
        ref={ref as any}
        {...props}
      >
        {children}
      </Component>
    );
  },
);

Text.displayName = "Text";
