import { cn } from "@/utils/cn";
import { useCallback, useState } from "react";
import { useDropzone, Accept } from "react-dropzone";
import { ImageUploadIcon, XIcon as CloseIcon } from "../icons";

export interface ImageMetadata {
  width: number;
  height: number;
  aspectRatio: number;
  size: number;
}

export interface ImageUploadProps {
  value?: File[];
  onChange?: (files: File[]) => void;
  maxFiles?: number;
  maxSize?: number;
  accept?: Accept;
  disabled?: boolean;
  className?: string;
  showPreview?: boolean;
  error?: boolean;
  onError?: (error: string) => void;
  validateImage?: (file: File, metadata: ImageMetadata) => string | null | Promise<string | null>;
  placeholder?: string;
  placeholderActive?: string;
}

/**
 *           .
 *
 * {@link ImageUpload}        .
 *   ,  ,    .
 *
 * ## When (  )
 *
 * **  :**
 * - **  **:     
 * - **  **:        
 * - ** **:      
 *
 * **   :**
 * - **  **:         FileUpload  
 * - ** **:           
 *
 * ## Example
 *
 * {@tool snippet}
 *   :
 *
 * ```tsx
 * <ImageUpload
 *   onChange={(files) => console.log(files)}
 *   maxFiles={1}
 *   maxSize={5 * 1024 * 1024} // 5MB
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *   :
 *
 * ```tsx
 * <ImageUpload
 *   onChange={(files) => console.log(files)}
 *   maxFiles={5}
 *   showPreview={true}
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *    :
 *
 * ```tsx
 * <ImageUpload
 *   validateImage={(file, metadata) => {
 *     if (metadata.width < 800 || metadata.height < 600) {
 *       return "  800x600  .";
 *     }
 *     return null;
 *   }}
 *   onError={(error) => alert(error)}
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *    :
 *
 * ```tsx
 * <ImageUpload
 *   validateImage={(file, metadata) => {
 *     if (metadata.width !== 1920 || metadata.height !== 1080) {
 *       return `  1920x1080 . (: ${metadata.width}x${metadata.height})`;
 *     }
 *     return null;
 *   }}
 *   onError={(error) => alert(error)}
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *   :
 *
 * ```tsx
 * <ImageUpload
 *   validateImage={(file, metadata) => {
 *     const targetRatio = 16 / 9;
 *     const tolerance = 0.1;
 *     if (Math.abs(metadata.aspectRatio - targetRatio) > tolerance) {
 *       return "  16:9 .";
 *     }
 *     return null;
 *   }}
 *   onError={(error) => alert(error)}
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *  :
 *
 * ```tsx
 * <ImageUpload
 *   validateImage={(file, metadata) => {
 *     if (metadata.width > 4000) {
 *       return "  4000px   .";
 *     }
 *     if (metadata.aspectRatio < 1) {
 *       return "    .";
 *     }
 *     if (metadata.size > 2 * 1024 * 1024) {
 *       return "  2MB   .";
 *     }
 *     return null;
 *   }}
 *   onError={(error) => alert(error)}
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *   :
 *
 * ```tsx
 * <ImageUpload
 *   placeholder="  "
 *   placeholderActive=" "
 *   onChange={(files) => console.log(files)}
 * />
 * ```
 * {@end-tool}
 */
export const ImageUpload = ({
  value = [],
  onChange,
  maxFiles = 1,
  maxSize = 5 * 1024 * 1024, // 5MB
  accept = { "image/*": [".png", ".jpg", ".jpeg", ".gif", ".webp"] },
  disabled = false,
  className,
  showPreview = true,
  error = false,
  onError,
  validateImage,
  placeholder = "  ",
  placeholderActive = "  ",
}: ImageUploadProps) => {
  const [files, setFiles] = useState<File[]>(value);

  const loadImageMetadata = (file: File): Promise<ImageMetadata> => {
    return new Promise((resolve, reject) => {
      const img = new Image();
      const url = URL.createObjectURL(file);

      img.onload = () => {
        URL.revokeObjectURL(url);
        resolve({
          width: img.width,
          height: img.height,
          aspectRatio: img.width / img.height,
          size: file.size,
        });
      };

      img.onerror = () => {
        URL.revokeObjectURL(url);
        reject(new Error("   ."));
      };

      img.src = url;
    });
  };

  const onDrop = useCallback(
    async (acceptedFiles: File[], rejectedFiles: any[]) => {
      if (rejectedFiles.length > 0) {
        const error = rejectedFiles[0].errors[0];
        if (error.code === "file-too-large") {
          onError?.(`  ${maxSize / 1024 / 1024}MB   .`);
        } else if (error.code === "file-invalid-type") {
          onError?.("   .");
        } else if (error.code === "too-many-files") {
          onError?.(` ${maxFiles}    .`);
        }
        return;
      }

      //    
      if (validateImage) {
        const validatedFiles: File[] = [];

        for (const file of acceptedFiles) {
          try {
            const metadata = await loadImageMetadata(file);
            const validationError = await validateImage(file, metadata);

            if (validationError) {
              onError?.(validationError);
              continue;
            }

            validatedFiles.push(file);
          } catch (error) {
            onError?.((error as Error).message);
          }
        }

        if (validatedFiles.length === 0) return;

        const newFiles = maxFiles === 1 ? validatedFiles : [...files, ...validatedFiles].slice(0, maxFiles);
        setFiles(newFiles);
        onChange?.(newFiles);
      } else {
        const newFiles = maxFiles === 1 ? acceptedFiles : [...files, ...acceptedFiles].slice(0, maxFiles);
        setFiles(newFiles);
        onChange?.(newFiles);
      }
    },
    [files, maxFiles, maxSize, onChange, onError, validateImage]
  );

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept,
    maxSize,
    maxFiles,
    disabled,
    multiple: maxFiles > 1,
  });

  const removeFile = (index: number) => {
    const newFiles = files.filter((_, i) => i !== index);
    setFiles(newFiles);
    onChange?.(newFiles);
  };

  const isSingleMode = maxFiles === 1;
  const hasFile = files.length > 0;
  const isMaxReached = files.length >= maxFiles;

  return (
    <div className={cn("w-full", className)}>
      {!(!isSingleMode && isMaxReached) && (
        <div
          {...getRootProps()}
          className={cn(
            "relative rounded-md border-2 border-solid",
            "transition-colors cursor-pointer",
            "flex flex-col items-center justify-center",
            "min-h-[200px]",
            error
              ? "border-red-500"
              : isDragActive
              ? "border-cms-black bg-cms-gray-100"
              : "border-cms-gray-300 bg-white hover:bg-cms-gray-50",
            disabled && "opacity-50 cursor-not-allowed pointer-events-none",
            isSingleMode && hasFile && "p-0"
          )}
        >
          <input {...getInputProps()} />

          {isSingleMode && hasFile && showPreview ? (
            <div className="relative w-full h-full min-h-[200px] group flex items-center justify-center bg-cms-gray-100 rounded-md overflow-hidden">
              <img
                src={URL.createObjectURL(files[0])}
                alt={files[0].name}
                className="max-w-full max-h-full object-contain"
              />
              <button
                type="button"
                onClick={(e) => {
                  e.stopPropagation();
                  removeFile(0);
                }}
                className={cn(
                  "absolute top-2 right-2",
                  "w-8 h-8 rounded-full",
                  "flex items-center justify-center",
                  "bg-white shadow-md",
                  "hover:bg-cms-gray-100",
                  "cursor-pointer",
                  "border-none"
                )}
                aria-label=" "
              >
                <CloseIcon className="w-4 h-4" />
              </button>
            </div>
          ) : (
            <div className="p-6 flex flex-col items-center">
              <ImageUploadIcon className="text-cms-gray-400" />
              <p className="mt-4 text-sm font-medium text-cms-black text-center">
                {isDragActive ? placeholderActive : placeholder}
              </p>
              <p className="mt-1 text-xs text-cms-gray-400 text-center">
                {maxFiles > 1 ? ` ${maxFiles}` : "1"} , {" "}
                {maxSize / 1024 / 1024}MB
              </p>
            </div>
          )}
        </div>
      )}

      {!isSingleMode && showPreview && files.length > 0 && (
        <div className="mt-4 grid grid-cols-2 gap-4 sm:grid-cols-3 md:grid-cols-4 justify-items-center">
          {files.map((file, index) => (
            <div
              key={index}
              className="relative group rounded-md overflow-hidden border border-cms-gray-300"
            >
              <div className="aspect-square bg-cms-gray-100">
                <img
                  src={URL.createObjectURL(file)}
                  alt={file.name}
                  className="w-full h-full object-cover"
                />
              </div>
              <button
                type="button"
                onClick={(e) => {
                  e.stopPropagation();
                  removeFile(index);
                }}
                className={cn(
                  "absolute top-2 right-2",
                  "w-7 h-7 rounded-full",
                  "flex items-center justify-center",
                  "bg-white shadow-md",
                  "hover:bg-cms-gray-100",
                  "cursor-pointer",
                  "border-none"
                )}
                aria-label=" "
              >
                <CloseIcon className="w-3 h-3" />
              </button>
              <div className="px-2 py-1.5 bg-white border-t border-cms-gray-300">
                <p className="text-xs text-cms-gray-600 truncate">{file.name}</p>
                <p className="text-xs text-cms-gray-400">
                  {(file.size / 1024).toFixed(1)} KB
                </p>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

ImageUpload.displayName = "ImageUpload";
