import React from "react";
import * as CheckboxPrimitive from "@radix-ui/react-checkbox";
import { Check } from "lucide-react";
import { cn } from "@/utils/cn";

export interface CheckboxProps extends React.ComponentPropsWithoutRef<
  typeof CheckboxPrimitive.Root
> {
  label?: string;
  id?: string;
}

/**
 *       ,        .
 *
 * {@link Checkbox}  On/Off  ,    
 *         .
 *
 * Radix UI Checkbox      
 *  .
 *
 * ## When (  )
 *
 * **  :**
 * - ** **:       
 * - ** **:         
 * - ** **:   /   
 * - ****:  (,  )     
 *
 * **   :**
 * - **  **:         `RadioButton` .
 * - **  **:       `Switch`     .
 *
 * ## Layout behavior
 *
 * - **Inline Alignment**:     ,     .
 * - **Spacing**:     (8px/0.5rem) .
 * - **Touch Area**:         `htmlFor`   .
 *
 * ## Usage guidelines
 *
 * ###  Do ( )
 *
 * - **  **:        .
 * - **  **: '  ' ' '       .
 * - ****:         .
 *
 * ###  Don't (/ )
 *
 * - **  **:         .
 * - ** **:       .      .
 *
 * ## Accessibility
 *
 * - **Keyboard Interaction**: `Space`       .
 * - **Focus Management**:       ,      .
 * - **WAI-ARIA**: `role="checkbox"` ,    `aria-checked`   .
 * - **Label Connection**: `id` `htmlFor`      .
 *
 * ## Example
 *
 * {@tool snippet}
 *    :
 *
 * ```tsx
 * <Checkbox
 *   label=" "
 *   checked={agreed}
 *   onCheckedChange={setAgreed}
 * />
 * ```
 * {@end-tool}
 *
 * {@tool snippet}
 *  :
 *
 * ```tsx
 * <Checkbox
 *   label="  "
 *   disabled
 *   checked={true}
 * />
 * ```
 * {@end-tool}
 *
 * See also:
 *
 * - {@link RadioButton},    
 * - {@link Switch},     
 * - {@link Button},     
 */
export const Checkbox = React.forwardRef<
  React.ElementRef<typeof CheckboxPrimitive.Root>,
  CheckboxProps
>(({ className, label, id, disabled, ...props }, ref) => {
  const checkboxId =
    id || `checkbox-${Math.random().toString(36).substr(2, 9)}`;

  return (
    <div className="flex items-center">
      <CheckboxPrimitive.Root
        ref={ref}
        id={checkboxId}
        disabled={disabled}
        className={cn(
          "peer h-5 w-5 shrink-0 rounded",
          "border border-gray-400 bg-white",
          "focus-visible:outline-none",
          "focus-visible:ring-2",
          "focus-visible:ring-offset-2",
          "disabled:cursor-not-allowed disabled:opacity-50",
          "data-[state=checked]:bg-black data-[state=checked]:border-black",
          "transition-colors",
          className
        )}
        {...props}
      >
        <CheckboxPrimitive.Indicator
          className={cn("flex items-center justify-center", "text-white")}
        >
          <Check className="h-[18px] w-[18px]" strokeWidth={4} />
        </CheckboxPrimitive.Indicator>
      </CheckboxPrimitive.Root>
      {label && (
        <label
          htmlFor={checkboxId}
          className={cn(
            "ml-2 text-base font-normal text-gray-500",
            "hover:text-black transition-colors",
            disabled && "cursor-not-allowed opacity-50",
            "cursor-pointer select-none"
          )}
        >
          {label}
        </label>
      )}
    </div>
  );
});

Checkbox.displayName = "Checkbox";
